a .button-container {
  position: relative;
  height: 50px;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 5px;
  z-index: -1; }
  a .button-container .button-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #FFFFFF;
    font-family: "Lato";
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase; }

a .button-container.quote {
  margin-top: -5px; }
