.menu-container {
  position: fixed;
  height: 60px;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  top: 0;
  left: 0;
  background: #FFFFFF; }
  .menu-container .menu__scb-logo {
    display: flex;
    flex-direction: row;
    align-items: center; }
    .menu-container .menu__scb-logo img {
      width: 100px;
      height: 50px; }
  .menu-container .menu__slides-progress {
    width: 60px;
    position: relative;
    margin-right: 15px;
    z-index: -1; }
    .menu-container .menu__slides-progress .menu__progress-container {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 55px;
      height: 24px;
      background: #5A6175;
      border-radius: 5px; }
      .menu-container .menu__slides-progress .menu__progress-container .menu__progress-indicator {
        position: absolute;
        text-align: center;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-family: "Lato";
        font-size: 16px;
        color: white; }
  .menu-container .menu__sidebar-container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    opacity: 1;
    left: -100%; }
    .menu-container .menu__sidebar-container .menu__sidebar-container--sidebar {
      width: 80vw;
      height: 100vh;
      background: white;
      z-index: 2;
      transition-duration: .3s;
      transition-timing-function: ease-in-out; }
    .menu-container .menu__sidebar-container .menu__sidebar-container--background {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      width: 100vw;
      height: 100vh;
      z-index: -1;
      background: rgba(67, 67, 67, 0.5);
      transition-delay: .1s;
      transition-duration: .3s; }
    .menu-container .menu__sidebar-container .menu__sidebar__top-container {
      display: flex;
      justify-content: space-between;
      position: relative;
      height: 104px; }
      .menu-container .menu__sidebar-container .menu__sidebar__top-container .menu__sidebar__logo-container {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        margin-left: 30px; }
        .menu-container .menu__sidebar-container .menu__sidebar__top-container .menu__sidebar__logo-container img {
          width: 100px;
          height: 50px; }
      .menu-container .menu__sidebar-container .menu__sidebar__top-container .menu__sidebar-close {
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%); }
      .menu-container .menu__sidebar-container .menu__sidebar__top-container::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background: #F6F6F6;
        bottom: 0; }
  .menu-container .menu__sidebar__list-container .menu__sidebar__list-container--ul {
    padding: 0;
    margin: 0; }
    .menu-container .menu__sidebar__list-container .menu__sidebar__list-container--ul .menu__sidebar__list-item {
      position: relative;
      height: 70px;
      list-style: none;
      text-transform: uppercase;
      font-family: 'Lato';
      font-weight: 400;
      font-size: 16px;
      color: #5A6175; }
      .menu-container .menu__sidebar__list-container .menu__sidebar__list-container--ul .menu__sidebar__list-item::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background: #F6F6F6;
        bottom: 0; }
      .menu-container .menu__sidebar__list-container .menu__sidebar__list-container--ul .menu__sidebar__list-item .item-title {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        margin-left: 30px; }
      .menu-container .menu__sidebar__list-container .menu__sidebar__list-container--ul .menu__sidebar__list-item .arrow {
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%); }
  .menu-container .menu__sidebar__entourage-container {
    height: 70px;
    width: 80vw;
    background: #F25E20;
    position: absolute;
    bottom: 0; }
    .menu-container .menu__sidebar__entourage-container .entourage-title {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      text-transform: uppercase;
      font-family: 'Lato';
      font-weight: 400;
      font-size: 16px;
      color: white;
      margin-left: 30px; }
    .menu-container .menu__sidebar__entourage-container .arrow {
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translateY(-50%);
      fill: white; }
