@font-face {
  font-family: "Lato-Black";
  src: "./../fonts/Lato-Black.ttf"; }

body {
  margin: 0;
  padding: 0; }

.data-container {
  width: 80%;
  height: 100%;
  margin-left: 15px;
  margin-top: 20vh; }
  .data-container .data-title {
    font-family: "Lato";
    font-weight: 900;
    color: #FFFFFF;
    font-size: 36px;
    width: 100%;
    height: 100%; }
  .data-container .entete__time-container {
    display: inline-block; }
    .data-container .entete__time-container .svg-clock {
      height: 18px;
      width: 18px;
      transform: translateY(15%); }
    .data-container .entete__time-container .time-text {
      margin-left: 4px;
      font-family: "Lato";
      font-size: 16px;
      font-weight: 400px;
      color: white; }

.back-button {
  margin-top: 30px;
  width: 20px;
  height: 18px;
  margin-left: 15px; }
