.character__image {
  width: 190px;
  height: 100px; }

.character-container.entete {
  position: absolute;
  bottom: calc(30px + 46px); }

.character-container .quote {
  position: relative; }

.character__bubble {
  width: 60vw;
  background: white;
  padding: 15px;
  margin-left: calc(30vw - 15px);
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
  position: relative;
  z-index: -1; }
  .character__bubble::after {
    content: "";
    width: 20px;
    height: 20px;
    background: white;
    transform: rotateZ(50deg);
    position: absolute;
    bottom: -5%; }
  .character__bubble span {
    font-family: 'Lato';
    font-size: 16;
    font-weight: 400;
    margin-left: 10px; }
  .character__bubble p {
    font-family: 'Lato';
    font-size: 18px;
    font-weight: 500;
    margin-left: 10px;
    margin-top: 2px;
    margin-bottom: 0; }
